                  "score":"0",
                  "analysis":"The Biden campaign did not submit a response to this question or any questions on the Plan To Win section, and we couldn’t find any significant details on his plans to reform agencies or rulemaking to enact a mobilization on the size and speed of the Green New Deal.",
