                  "score":"3",
                  "analysis":"Sen. Sanders’ plan both answers the call for direct financing and intends to use the office of the President to foster international diplomacy on climate action. He calls for the US to invest $200 billion in the Green Climate Fund and aid less affluent countries in decarbonization and also calls for the US to lead industrialized nations in divesting from military financing and fossil fuels assets",
                  "source":"From https://berniesanders.com/en/issues/green-new-deal/\n\n\"In order to help countries of the Global South with climate adaptation efforts, the U.S. will invest $200 billion in the Green Climate Fund for the equitable transfer of renewable technologies, climate adaptation, and assistance in adopting sustainable energies. U.S. leadership can ensure that the developing world secures reliable electricity, reduces poverty and pollution-related fatalities, creates greater net employment, and improves living standards — all while reducing greenhouse-gas emissions.”\n\n“Bring together the leaders of the major industrialized nations with the goal of using the trillions of dollars our nations spend on misguided wars and weapons of mass destruction to instead work together internationally to combat our climate crisis and take on the fossil fuel industry. Bernie recognizes that the Pentagon is the largest institutional emitter of greenhouse gases in the world and that the United States spends $81 billion annually to protect oil supplies and transport routes. We are uniquely positioned to lead the planet in a wholesale shift away from militarism.”\n\n“Not only have agreements like NAFTA and Permanent Normal Trade Relations with China outsourced millions of American jobs, they have allowed corporations to outsource their pollution. Trade deals have been written in secret by billion-dollar companies to give polluters special handouts and protections, as well as the right to sue governments that pursue stronger environmental protections. Under a Sanders Administration, this will end. Trade deals will be renegotiated to ensure strong and binding climate standards, labor rights, and human rights with swift enforcement.”\n\n“End overseas fossil fuel financing. The federal government currently supports investments in fossil fuels through the World Bank, the International Monetary Fund, OPIC, the Export-Import Bank, and other multilateral institutions. These international investments are inconsistent with a goal to curb the global climate crisis and must end. A Sanders Administration will lead these international financial institutions in advancing the equitable adoption of sustainable energy across the planet.”\n“Meeting and exceeding our fair share of global emissions reductions. The United States has for over a century spewed carbon pollution emissions into the atmosphere in order to help less industrialized nations meet their targets while improving quality of life. We will reduce domestic emissions by at least 71 percent by 2030 and reduce emissions among less industrialized nations by 36 percent by 2030 — the total equivalent of reducing our domestic emissions by 161 percent.”"
