                  "source":"From https://joebiden.com/empowerworkers/ & https://joebiden.com/climate-labor-fact-sheet/\n\n“President Biden will defend workers’ rights to form unions and collectively bargain in these emerging and growing industries; pursue new partnerships with community colleges, unions, and the private sector to develop programs to train all of America’s workforce to tap into the growing clean energy economy; incorporate skills training into infrastructure investment planning by engaging state and local communities; and reinvigorate and repurpose AmeriCorps for sustainability, so that every American can participate in the clean energy economy. These efforts will be worker-centered and driven in collaboration with the communities they will affect.”\n\n\"Biden will go beyond the PRO Act by enacting legislation to impose even stiffer penalties on corporations and to hold company executives personally liable when they interfere with organizing efforts, including criminally liable when their interference is intentional.\"\n\n\"Ensure federal dollars do not flow to employers who engage in union-busting activities, participate in wage theft, or violate labor law.\"\n\n\"Too many employers pretend to bargain with unions (“surface bargaining”) with no intent of reaching an agreement. Biden will give the NLRB the necessary power to force any employer found to be bargaining in bad faith back to the negotiating table, as called for in the PRO Act. And, he will require those companies to pay a penalty, in addition to making workers whole for the time the company stalled negotiations.\""
