                  "source":"From https://berniesanders.com/en/issues/green-new-deal/\n\n\"Currently, four federal Power Marketing Administrations (PMAs) and the Tennessee Valley Authority generate and transmit power to distribution utilities in 33 states.We will create one more PMA to cover the remaining states and territories and expand the existing PMAs to build more than enough wind, solar, energy storage and geothermal power plants. We will spend $1.52 trillion on renewable energy and $852 billion to build energy storage capacity. Together, with an EPA federal renewable energy standard, this will fully drive out non-sustainable generation sources.”\n\n“A smart grid means a resilient, secure, and intelligent electric grid system that is capable of managing high amounts of renewable energy, charging electric vehicles quickly, and maximizing efficiency. We will spend $526 billion on a modern, high-volt, underground, renewable, direct current, smart, electric transmission and distribution grid will ensure our transition to 100 percent sustainable energy is safe and smooth.”\n\n“In 2013, 800,000 gallons of crude oil was spilled in railroad accidents. In 2014, an average of one oil train derailed every five days. The Federal Railroad Administration will adopt new rules requiring companies to retrofit the coal and oil bomb trains to prevent explosions, derailments, and spills. We will take similar action to protect communities’ well pads, substations, compressor stations, and pipelines as we remove fossil fuels to better protect communities that never asked to be cited in their footprint.”"
