                  "source":"From https://joebiden.com/climate/\n\n\"There are now one million electric vehicles on the road in the United States. But a key barrier to further deployment of these greenhouse-gas reducing vehicles is the lack of charging stations and coordination across all levels of government. As President, Biden will work with our nation’s governors and mayors to support the deployment of more than 500,000 new public charging outlets by the end of 2030. In addition, Biden will restore the full electric vehicle tax credit to incentivize the purchase of these vehicles. He will ensure the tax credit is designed to targeted middle class consumers and, to the greatest extent possible, to prioritize the purchase of vehicles made in America. And, he will work to develop a new fuel economy standard that goes beyond what the Obama-Biden Administration put in place.”"
