                  "source":"From https://joebiden.com/climate/\n\n“Enacting a national strategy to develop a low-carbon manufacturing sector in every state, accelerating cutting-edge technologies and ensuring businesses and workers have access to new technologies and skills, with a major focus on helping small and large manufacturers upgrade their capabilities to have both competitive and low-carbon futures. The strategy will connect research universities, community colleges, incubators and accelerators, manufacturing institutes and employers, unions, and state and local governments – alone or as part of a regional pact – and provide them with significant funding for deployment of a place-based plan to help their state or region build a competitive and low-carbon future in manufacturing that reflects climate impacts in their local communities. Industries from textiles to machine tools to metal fabrication to the most advanced manufacturing technologies will be eligible for funding to modernize, compete, create jobs, and move to clean energy futures. Allocated tax credits and subsidies will be available for businesses to upgrade equipment and processes, invest in expanded or new factories, and deploy low-carbon technologies, as long as all stakeholders are part of the process of determining a bottom-line win for jobs, workers, clean energy, and long-term community investment. Where states feel competitive pressures or requirements in response to the climate emergency may threaten a local economy, President Biden’s national strategy will fund efforts to move to a more competitive or low-carbon manufacturing approach that can preemptively develop new economic strategies, including deployment of federal funding for technologies or manufacturing innovation centers.”"
