                  "source":"From https://joebiden.com/climate/\n\n“Partnering with farmers and ranchers so that better agriculture practices and deployment of digesters generate new sources of revenues. For our family farmers, ranchers, and landowners the climate agenda is not just about growing nutritious food and making it accessible to all families, it’s also about having water they can rely on for growing that food. It’s about local farms and fresh food for every community. And it’s about making sure that floodwaters in the Midwest are not taking away family farms that have fed our people for decades. Biden will review regulatory roadblocks to new innovations and invest in climate-friendly farming such as conservation programs for cover crops and other practices aimed at restoring the soil and building soil carbon, and in the process, preventing run-off and helping family farmers deploy the latest technologies to maximize productivity. He will create new opportunities to support deployment of methane digesters to capture potent climate emissions and generate electricity. With these efforts, family farmers can benefit and help lead the Clean Energy Revolution.”"
