                  "source":"From https://elizabethwarren.com/plans/new-farm-economy & https://medium.com/@teamwarren/leveling-the-playing-field-for-americas-family-farmers-823d1994f067 \n\n“As President, I will lead a full-out effort to decarbonize the agricultural sector by investing in our farmers and giving them the tools, research, and training they need to transform the sector — so that we can achieve the objectives of the Green New Deal to reach net-zero emissions by 2030.”\n\n“My plan will make it economically feasible for farmers to be part of the climate change solution by increasing CSP’s payments for sustainable farming practices from around $1 billion today to $15 billion annually — and expanding the types of practices eligible for compensation — so that every farmer who wants to use their land to fight climate change can do so.”\n\n“I will use every tool at my disposal to level the playing field for family farmers and hold agribusinesses accountable for the damage they’ve wrought on our farmland.”\n\n“My administration will expand the “Farm-to-School” program a hundredfold and turn it into a billion-dollar \n\n“Farm to People” program in which all federally-supported public institutions — including military bases and hospitals — will partner with local, independent farmers to provide fresh, local food.\"\n\n\"I will dedicate resources from the $400 billion R&D commitment in my Green Manufacturing Plan towards innovations for decarbonizing the agriculture sector, including a farmer-led Innovation Fund that farmers can apply to use towards pioneering new methods of sustainable farming, like agroforestry.\""
